import {connect, Provider} from "react-redux";
import store, {StateType} from "./redux/redux-store";
import {getInitialized} from "./redux/app-reducer";
import {HashRouter} from "react-router-dom";
import React from "react";
import App from "./App";

const mapStateToProps = (state: StateType) => ({});

const AppContainer = connect<MapStatePropsType, MapDispatchPropsType, {}, StateType>
(mapStateToProps, {initializeApp: getInitialized})(App)

type MapStatePropsType = {};

type MapDispatchPropsType = {
    initializeApp: () => void
};

export type AppPropsType = MapStatePropsType & MapDispatchPropsType;

const AppGlobal = () => {
    return <HashRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </HashRouter>
}

export default AppGlobal;